*,
*::after,
*::before {
    list-style: none;
    text-transform: none;
    text-decoration: none;
    text-align: none;
    color: #000;
    padding: 0;
    margin: 0;
    border: none;
    outline: none;
}

body {
    font-family: "Roboto", sans-serif;
    flex-direction: column;
    margin: auto;
    align-content: center;
}
.container {
    //max-width: 1600px;
    min-width: 320px;
    display: flex;
    margin: 0 auto;
    align-items: center;
    justify-content: center; 
}



